import React, {
    useRef,
    useState,
    cloneElement,
    isValidElement,
    Children,
    useEffect,
    useMemo,
    useCallback,
    SyntheticEvent
} from "react";
import { usePosition } from "../../hooks/usePosition";
import { clsx } from '../../helpers';
import { PopoverProps } from "../../types/popover";
import { ConditionalWrapper } from '../ConditionalWrapper';
import { createPortal } from 'react-dom';
import { prefixClsPopover } from "../../utils";
import { flattenChildren } from "../../helpers/flatten";
import './style.css';

const Popover = ({
    prefixCls = prefixClsPopover,
    content,
    children,
    trigger = "click",
    placement = "bottom",
    open,
    visible,
    title,
    style = {},
    overlayClassName = '',
    overlayStyle = {},
    listenPopoverPositions,
    placementPositionOffset,
    onVisibleChange,
    getPopupContainer
}: PopoverProps) => {
    const triggerRef = useRef<HTMLDivElement>(null);
    const popupRef = useRef<HTMLDivElement>(null);

    const [innerOpen, setInnerOpen] = useState(false);

    const isOpen = visible !== undefined ? visible : open !== undefined ? open : innerOpen;

    const { dropdownPosition, showPlacement } = usePosition({
        isOpen,
        offset: 10,
        popupRef,
        placement,
        triggerRef,
        listenPopoverPositions,
        placementPositionOffset,
        getPopupContainer: getPopupContainer?.(triggerRef.current as HTMLElement)
    });

    useEffect(() => {
        const handleClickOutside = (e: MouseEvent) => {
            if (
                popupRef.current &&
                !popupRef.current.contains(e.target as Node) &&
                triggerRef.current &&
                !triggerRef.current.contains(e.target as Node)
            ) {
                setInnerOpen(false);
                onVisibleChange?.(false);
            }
        };

        document.addEventListener('mousedown', handleClickOutside);

        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    const triggers = Array.isArray(trigger) ? trigger : [trigger];

    const handleOnClick = useCallback((e: SyntheticEvent) => {
        const newState = !isOpen;

        if (triggers.includes('click')) {
            onVisibleChange?.(newState);
            setInnerOpen(newState);
        }

    }, [isOpen, triggers]);

    const handleOnMouseEnter = useCallback(() => {
        if (triggers.includes("hover")) {
            onVisibleChange?.(true);
            setInnerOpen(true);
        }
    }, [triggers]);

    const handleOnMouseLeave = useCallback(() => {
        if (triggers.includes("hover")) {
            onVisibleChange?.(false);
            setInnerOpen(false);
        }
    }, [triggers]);

    const childProps = useMemo(() => ({
        ...(triggers.includes("click") ? { onClick: handleOnClick } : {}),
        ...(triggers.includes("hover") ? { onMouseEnter: handleOnMouseEnter, onMouseLeave: handleOnMouseLeave } : {})
    }), [triggers]);

    const _children = useMemo(() => {
        if (Children.count(children) > 1) {
            children = <div>{children}</div>
        }

        return Children.map(children, (child, index) => {
            if (!isValidElement(child)) {
                child = (
                    <div key={index ?? `popover-child-${index}`}>
                        {child}
                    </div>
                )
            }

            return cloneElement(child, {
                key: index ?? `popover-child-${index}`,
                ...{
                    style,
                    ...childProps,
                    ref: triggerRef,
                    className: `${prefixCls}-wrapper-content`,
                },
            })
        })
    }, [children, style])

    const _content = useMemo(() => flattenChildren(content), [content]);

    return (
        <>
            {_children}

            {isOpen && (
                <ConditionalWrapper
                    condition={!!getPopupContainer}
                    wrapper={(element) =>
                        getPopupContainer
                            ? createPortal(element, getPopupContainer(triggerRef.current as HTMLElement) as HTMLElement)
                            : <>{element}</>
                    }
                >
                    <div
                        ref={popupRef}
                        {...childProps}
                        className={clsx(prefixCls, `${prefixCls}-${placement}`, overlayClassName)}
                        style={{
                            zIndex: 10000,
                            position: "absolute",
                            ...overlayStyle,
                            ...dropdownPosition
                        }}
                    >
                        {title && <div className={`${prefixCls}-title`}>{title}</div>}
                        <div className={`${prefixCls}-inner`}>
                            {Children.map(_content, (child, index) => <div key={index}>{child}</div>)}
                        </div>
                        <div className={`${prefixCls}-arrow ${showPlacement}`} />
                    </div>
                </ConditionalWrapper>
            )}
        </>
    );
};

export default Popover;
